@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.header__wrapper {
  margin: 0 auto;
  padding: 0 30px;
  position: relative;
}
.header__logo-link {
  height: 79px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo-link[href]:hover {
  opacity: 0.6;
}
.header__logo-link[href]:active {
  opacity: 0.3;
}
.header__logo-img {
  margin-top: 5px;
}
.header__menu-link {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  display: block;
  padding: 27.5px 0;
  padding-left: 46px;
}
.header__menu-link:hover .header__search-svg,
.header__menu-link:hover .header__cart-svg {
  fill: #63d1bb;
}
.header__menu-link[href]:hover,
.header__product-menu-link[href]:hover {
  color: rgba(0, 0, 0, 0.6);
}
.header__menu-link[href]:active,
.header__product-menu-link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.header__menu-item--search,
.header__menu-item--cart {
  position: relative;
}
.header__search-svg,
.header__cart-svg {
  position: absolute;
  left: 0;
  top: 30px;
}
.header__toggle {
  background: none;
  border: none;
  position: absolute;
  width: 20px;
  height: 20px;
  right: 30px;
  top: 29px;
  padding: 0;
}
