    margin-bottom: 30px;
  }
  .form__type-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
  }
  .form__title {
    font-size: 17px;
    line-height: 30px;
    width: 25%;
    margin: 0;
    margin-top: 13px;
    padding-right: 0;
  }
  .form__title::before {
    display: none;
  }
  .form__type-label {
    font-size: 19px;
    line-height: 18px;
    padding: 18px 0 18px 81px;
    margin-bottom: 21px;
  }
  .form__type-list {
    width: 75%;
  }
  .form__color-group {
    background-color: #f1f1f1;
    margin-bottom: 52px;
  }
  .form__color-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    padding-top: 48px;
    padding-bottom: 27px;
  }
  .form__color-list {
    width: 75%;
    display: flex;
    flex-wrap: wrap;
  }
  .form__color-item {
    width: 47.9%;
    margin-bottom: 22px;
  }
  .form__color-item:nth-child(2n) {
    margin-left: auto;
  }
  .form__color-label {
    font-size: 19px;
    line-height: 18px;
    padding: 18px 0 18px 81px;
    margin-bottom: 0;
  }
  .form__name-group {
    margin-bottom: 25px;
  }
  .form__name-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
  }
  .form__title--name {
    margin-top: 10px;
  }
  .form__name-info-wrap {
    width: 75%;
  }
  .form__name-label {
    font-size: 19px;
    line-height: 18px;
    margin-right: 25px;
    max-width: 400px;
  }
  .form__name-wrap {
    display: flex;
    align-items: center;
    margin-top: 0;
    margin-bottom: 27px;
  }
  .form__text-inp {
    padding-left: 16px;
  }
  .form__text-inp::placeholder,
  .form__textarea::placeholder {
    font-size: 19px;
    line-height: 18px;
  }
  .form__tel {
    background-color: #f1f1f1;
    margin-bottom: 0;
  }
  .form__tel-wrap {
    margin: 0 auto;
    padding: 0 50px;
    padding-top: 48px;
    display: flex;
  }
  .form__text-inp--tel {
    background-color: #f1f1f1;
    margin-left: 43px;
    margin-top: 5px;
    background-position: top 5px right 26px;
  }
  .form__mail {
    background-color: #f1f1f1;
  }
  .form__mail-wrap {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    padding-top: 22px;
    padding-bottom: 50px;
  }
  .form__text-inp--mail {
    background-color: #f1f1f1;
    margin-left: 43px;
    margin-top: 5px;
    background-position: top 5px right 23px;
  }
  .form__add {
    margin-bottom: 54px;
  }
  .form__add-wrap {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    padding-top: 30px;
  }
  .form__textarea {
    margin-left: 43px;
  }
  .form__bottom {
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    align-items: center;
    margin-bottom: 49px;
  }
  .form__button {
    width: 34.6%;
    order: -1;
  }
  .form__note {
    font-size: 17px;
    line-height: 30px;
    margin-left: auto;
    margin-bottom: 8px;
  }
}
@media (min-width: 1150px) {
  .form__wrapper {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
    padding-top: 124px;
    padding-bottom: 0;
  }
  .form__description {
    width: 66.7%;
    margin: 0 auto;
    margin-bottom: 55px;
  }
  .form__type-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
  }
  .form__title {
    width: 175px;
    padding-left: 63px;
  }
  .form__color-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
    padding-top: 46px;
    padding-bottom: 29px;
  }
  .form__color-list {
    width: 83.3%;
  }
  .form__color-item--right {
    margin-left: 0;
  }
  .form__color-item {
    width: 24%;
    margin-right: 53px;
  }
  .form__color-item:nth-child(2n) {
    margin-left: 0;
  }
  .form__color-item:nth-child(3n) {
    margin-right: 0;
  }
  .form__name-group-wrap {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
  }
  .form__name-info-wrap {
    width: 66.7%;
  }
  .form__tel-wrap,
  .form__mail-wrap,
  .form__add-wrap {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
    padding-right: 225px;
  }
  .form__tel-wrap {
    padding-top: 47px;
  }
  .form__mail-wrap {
    padding-top: 23px;
    padding-bottom: 48px;
  }
  .form__text-inp--tel {
    background-position: top 5px right 8px;
  }
  .form__text-inp--mail {
    background-position: top 5px right 6px;
  }
  .form__add-wrap {
    padding-top: 32px;
  }
  .form__bottom {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
    padding-left: 225px;
    padding-right: 225px;
    margin-bottom: 50px;
  }
  .form__text-inp--tel,
  .form__text-inp--mail,
  .form__textarea {
    margin-left: 0;
    width: 80%;
  }
  .form__add {
    margin-bottom: 50px;
  }
  .form__button {
    width: 32.9%;
  }
}
